    // @ts-ignore
    InputStepper.displayName = "InputStepper";
    // @ts-ignore
    InputStepper.__docgenInfo = { "description": "Renders a numeric step input with an embedded label and a custom set of step buttons", "displayName": "InputStepper", "props": { "label": { "defaultValue": null, "description": "Label to show beneath the numeric value", "name": "label", "required": true, "type": { "name": "string" } }, "value": { "defaultValue": null, "description": "Value to assign the numeric field", "name": "value", "required": true, "type": { "name": "number" } }, "max": { "defaultValue": { value: 999 }, "description": "Maximum value allowed", "name": "max", "required": false, "type": { "name": "number" } }, "min": { "defaultValue": { value: 0 }, "description": "Minimum value allowed", "name": "min", "required": false, "type": { "name": "number" } }, "onChange": { "defaultValue": null, "description": "Action to perform when the value of this field has changed", "name": "onChange", "required": true, "type": { "name": "(val: number) => void" } }, "ariaLabel": { "defaultValue": null, "description": "Label to use for accessibility purposes; will be read on the label, the\ninput field itself, and the stepper buttons via aria-labelledby", "name": "ariaLabel", "required": true, "type": { "name": "string" } }, "onStepperButtonClick": { "defaultValue": null, "description": "Tracking callback to run when the step buttons are clicked", "name": "onStepperButtonClick", "required": false, "type": { "name": "(target: InputStepperButtonTargets) => void" } }, "className": { "defaultValue": null, "description": "Any additional styling that should be applied to the stepper", "name": "className", "required": false, "type": { "name": "string" } } } };
